<template>
  <el-main>
    <el-row :gutter="16" class="row-1">
      <el-col :span="8">
        <el-card>
          <div class="stream" id="stream-1"></div>
          <el-button type="primary" @click="createClient">加入</el-button>
          <el-button @click="leave">离开</el-button>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card>
          <div class="stream" id="stream-2"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card>
          <div class="stream" id="stream-3"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="16" class="row-2">
      <el-col :span="6">
        <el-card>
          <div class="stream" id="stream-4"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <div class="stream" id="stream-5"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <div class="stream" id="stream-6"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <div class="stream" id="stream-7"></div>
          <el-button icon="el-icon-turn-off-microphone"></el-button>
        </el-card>
      </el-col>
    </el-row>
  </el-main>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  data() {
    return {
      client: null,
      clientConfig: {
        mode: "live",
        codec: "vp8"
      },
      joined: false,
      published: false,
      streamList: [],
      appId: "4bf7080a32b348209ba5a50a5190b7d4",
      channel: "HakunaMatata",
      uid: null,

      streamMap: {}
    };
  },
  beforeCreate() {
    console.log(
      "agora sdk version: " +
        this.AgoraRTC.VERSION +
        " compatible: " +
        this.AgoraRTC.checkSystemRequirements()
    );
  },
  created() {
    this.AgoraRTC.getDevices(
      function(devices) {
        console.log(devices);
      },
      function(errStr) {
        console.error("Failed to getDevice", errStr);
      }
    );
  },
  methods: {
    createClient() {
      this.client = this.AgoraRTC.createClient(this.clientConfig);
      this.client.init(
        this.appId,
        () => {
          console.log("client initialized");
          this.handleEvents();
          // Join a channel
          this.joinChannel();
        },
        err => {
          console.log("client init failed ", err);
          // Error handling
        }
      );
    },

    joinChannel() {
      this.client.join(
        null,
        this.channel,
        null,
        uid => {
          this.uid = uid;
          this.joined = true;
          console.log("client" + uid + "joined channel");
          // Create a local stream
          this.createStream();
        },
        err => {
          console.error("client join failed ", err);
          // Error handling
        }
      );
    },
    createStream() {
      let stream = this.AgoraRTC.createStream({
        streamID: this.uid,
        audio: true,
        video: true,
        screen: false
      });

      stream.init(
        () => {
          console.log("local stream initialized");
          this.streamList.push(stream);
          this.streamMap[stream.getId()] = "stream-" + this.streamList.length;
          // play stream with html element          
          stream.play(this.streamMap[stream.getId()]);
          // publish the stream
          this.publish(stream);
        },
        err => {
          console.error("local stream init failed ", err);
          //error handling
        }
      );
    },

    publish(stream) {
      this.client.publish(stream, err => {
        console.log(err);
        //……
      });
    },
    unpublish(stream) {
      if (this.client === null) {
        alert("Please Join Room First");
        return;
      }
      if (!this.published) {
        alert("还没 publish");
        return;
      }
      var oldState = this.published;
      this.client.unpublish(stream, err => {
        this.published = oldState;
        console.log("unpublish failed");
        alert("unpublish failed");
        console.error(err);
      });
      alert("unpublish");
      this.published = false;
    },
    subscribe(remoteStream) {
      this.client.subscribe(remoteStream, err => {
        console.log("stream subscribe failed", err);
      });
    },
    handleEvents() {
      // Occurs when an error message is reported and requires error handling.
      this.client.on("error", err => {
        console.log(err);
      });
      // Occurs when the local stream is published.
      this.client.on("stream-published", evt => {
        this.published = true;
        alert("stream published success");
        console.log("stream-published", evt);
      });

      // Occurs when the remote stream is added.
      this.client.on("stream-added", evt => {
        var remoteStream = evt.stream;
        var id = remoteStream.getId();
        alert("stream-added uid: " + id);
        if (id !== this.uid) {
          this.subscribe(remoteStream);
        }
        console.log("stream-added remote-uid: ", id);
      });

      // Occurs when a user subscribes to a remote stream.
      this.client.on("stream-subscribed", evt => {
        var remoteStream = evt.stream;
        this.streamList.push(remoteStream);

        this.streamMap[remoteStream.getId()] = "stream-" + this.streamList.length;

        remoteStream.play(this.streamMap[remoteStream.getId()]);

        alert("stream-subscribed remote-uid: " + remoteStream.getId());
        console.log("stream-subscribed remote-uid: ", remoteStream.getId());
      });

      // Occurs when the remote stream is removed; for example, a peer user calls Client.unpublish.
      this.client.on("stream-removed", evt => {
        var remoteStream = evt.stream;
        var id = remoteStream.getId();
        alert("stream-removed uid: " + id);
        remoteStream.stop();
        this.streamList = this.streamList.filter(stream => {
          return stream.getId() !== id;
        });

        console.log("stream-removed remote-uid: ", id);
      });

      // Occurs when the peer user leaves the channel; for example, the peer user calls Client.leave.
      this.client.on("peer-leave", evt => {
        var id = evt.uid;
        console.log("id", evt);
        if (id !== this.uid) {
          this.streamList = this.streamList.filter(item => item.getId() !== id);
        }
        alert("peer leave");
        console.log("peer-leave", id);
      });
    },
    leave() {
      if (this.client === null) {
        alert("Please Join First!");
        return;
      }
      if (!this.joined) {
        alert("You are not in channel");
        return;
      }
      /**
       * Leaves an AgoraRTC Channel
       * This method enables a user to leave a channel.
       **/
      this.client.leave(
        () => {
          this.streamList.forEach(stream => {
            // local stream
            if (stream.getId() === this.uid) {
              // stop stream
              stream.stop();
              // close stream
              stream.close();
            }
          });

          this.streamList.forEach(stream => {
            // stop stream
            stream.stop();
            // close stream
            stream.close();
          });

          this.streamList = [];
          this.client = null;
          console.log("client leaves channel success");
          this.published = false;
          this.joined = false;
          alert("leave success");
        },
        err => {
          console.log("channel leave failed");
          alert("leave success");
          console.error(err);
        }
      );
    }
  }
};
</script>

